import React, { PureComponent } from "react";
import { View, StyleSheet } from "react-native";
import Text from "../../Text/@1.0";
import Image from "../../Image/@1.0";

interface LoadingProps {
  /**
   * loading窗提示内容
   */
  message?: string;
}

/**
 * Loading窗
 */
class Loading extends PureComponent<LoadingProps> {
  render() {
    return (
      <View style={styles.box}>
        <Image style={styles.img} source={require("./loading.gif")} />
        <Text style={styles.text}>{this.props.message || "加载中"}...</Text>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  siblingY: {
    flex: 1,
    width: "100%",
  },
  box: {
    backgroundColor: "rgba(0,0,0,0.7)",
    width: 140,
    paddingTop: 58,
    paddingBottom: 10,
    borderRadius: 6,
  },
  img: {
    position: "absolute",
    top: 15,
    left: "50%",
    marginLeft: -16,
    width: 32,
    height: 32,
    borderWidth: 0,
  },
  text: {
    color: "#fff",
    textAlign: "center",
    paddingHorizontal: 5,
  },
});

/**
 * 创建loading
 */
export default (props: LoadingProps) => <Loading {...props} />;
