import React, { PureComponent } from "react";
import { View } from "react-native";

interface SectionGapProps {
  /**
   * 高度，默认是30
   */
  height?: number;
  /**
   * 背景颜色，默认是透明的
   */
  backgroundColor?: string;
}

/**
 * 区块间距
 * 1. 主要用于页面布局时，在两个区块间增加间隔
 * 2. 使用这个组件比使用padding、magin更直观和方便
 * 3. 默认高度为30，背景为透明
 */
export default class SectionGap extends PureComponent<SectionGapProps> {
  static defaultProps = {
    height: 30,
    backgroundColor: "transparent"
  };
  
  render() {
    const { height, backgroundColor } = this.props;
    return <View style={{ width: "100%", height, backgroundColor }} />;
  }
}
