import SessionCacheBase from "./SessionCacheBase";

interface SessionCacheEntity<T> {
  value: T;
}

/**
 * 会话缓存类
 */
class SessionCache extends SessionCacheBase {
  private cacheKeyOfAllCacheKeyListInCurrentApp = `react_native_essential_session_cache_all_keys_of_${this.appName}`;
  private allCacheKeyList: string[] = [];

  constructor(protected readonly appName: string) {
    super(appName);

    this.getAllCacheKeyList = this.getAllCacheKeyList.bind(this);
    this.updateAllCacheKeyList = this.updateAllCacheKeyList.bind(this);
    this.get = this.get.bind(this);
    this.set = this.set.bind(this);
    this.remove = this.remove.bind(this);
    this.clear = this.clear.bind(this);

    this.getAllCacheKeyList();
  }

  private getAllCacheKeyList() {
    const allKeys = sessionStorage.getItem(
      this.cacheKeyOfAllCacheKeyListInCurrentApp
    );
    if (allKeys) {
      this.allCacheKeyList = allKeys.split(",");
    }
  }

  private updateAllCacheKeyList() {
    if (this.allCacheKeyList.length > 0) {
      const allKeys = sessionStorage.getItem(
        this.cacheKeyOfAllCacheKeyListInCurrentApp
      );

      // 合并其它地方用到的key
      if (allKeys) {
        allKeys.split(",").forEach((k) => {
          this.allCacheKeyList.indexOf(k) == -1 && this.allCacheKeyList.push(k);
        });
      }

      sessionStorage.setItem(
        this.cacheKeyOfAllCacheKeyListInCurrentApp,
        this.allCacheKeyList.join(",")
      );
    }
  }

  get<T = string>(key: string): T | undefined {
    try {
      key = this.buildKey(key);
      const { value } = JSON.parse(
        sessionStorage.getItem(key) || ""
      ) as SessionCacheEntity<T>;
      return value;
    } catch (error) {
      return undefined;
    }
  }

  set<T>(key: string, value: T) {
    if (this.allCacheKeyList.findIndex((k) => k == key) == -1) {
      this.allCacheKeyList.push(key);
      this.updateAllCacheKeyList();
    }

    const wrapper: SessionCacheEntity<T> = { value };
    try {
      key = this.buildKey(key);
      sessionStorage.setItem(key, JSON.stringify(wrapper));
    } catch (error) {
      return false;
    }
  }

  remove(key: string) {
    const index = this.allCacheKeyList.findIndex((k) => k == key);
    if (index != -1) {
      this.allCacheKeyList.splice(index, 1);
      this.updateAllCacheKeyList();
    }

    sessionStorage.removeItem(this.buildKey(key));
  }

  clear() {
    this.getAllCacheKeyList();
    this.allCacheKeyList.forEach((key) => this.remove(key));
    this.allCacheKeyList = [];
    sessionStorage.removeItem(this.cacheKeyOfAllCacheKeyListInCurrentApp);
  }
}

/**
 * SessionCacheProvider，执行该函数获得SessionCache类实例
 * @param appName 项目app名称，用于区分不同App的数据
 */
export default (appName: string) => new SessionCache(appName);
